import {Component} from 'react';
import './NavStyle.css';

class Nav extends Component{
    render(){
        const menu = this.props.menu;
        var back = ['white', , , , ];
        var font = ['black', , , , ];
        if(menu == 'Home'){ back=['white', , , , ]; font=['black', , , , ]; }
        if(menu == 'People'){ back=[ , 'white', , , ]; font=[ , 'black', , , ]; }
        if(menu == 'Research'){ back=[ , , 'white', , ]; font=[ , , 'black', , ]; }
        if(menu == 'Publication'){ back=[ , , , 'white', ]; font=[ , , , 'black', ]; }
        if(menu == 'Software'){ back=[ , , , , 'white']; font=[ , , , , 'black']; }


        return(
            <div className='nav'>
                <button id='nav-menu' onClick={() => this.props.changeMenu('Home')}>Home</button>
                <button id='nav-menu' style={{color:font[1], background:back[1]}} onClick={() => this.props.changeMenu('People')}>People</button>
                <button id='nav-menu' style={{color:font[2], background:back[2]}} onClick={() => this.props.changeMenu('Research')}>Research</button>
                <button id='nav-menu' style={{color:font[3], background:back[3]}} onClick={() => this.props.changeMenu('Publication')}>Publication</button>
                <button id='nav-menu' style={{color:font[4], background:back[4]}} onClick={() => this.props.changeMenu('Software')}>Software</button>
            </div>
        );
    }
}

export default Nav;