import {Component} from 'react';
import './PageStyle.css';

class Software extends Component{
    render(){
        return(
            <div className='soft'>
                <div id='soft-top'>
                    <p id='soft-title'>Software</p>
                </div>

                <div id='soft-section'>
                    <div id='img'>image input</div>
                    <div id='soft-info'>
                        <p>explain text for software</p>
                        <button>github tag</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Software;