import {Component} from 'react';
import './PageStyle.css';

import linkimg from './image/link.png';
import circle from './image/circle.png';
import ComIntelligence from './data/Computational-Intelligence.json';
import ComFinance from './data/Computational-Finance.json';
import ComBiology from './data/Computational-Biology.json';

class Publication extends Component{
    constructor(props){
        super(props);
        this.state = {
            pub_kind: 'intelligence'
        }
        this.changePubKind = this.changePubKind.bind(this);
        this.goDOI = this.goDOI.bind(this);
    }

    changePubKind(_kind){
        this.setState({pub_kind: _kind});
    }

    goDOI(_doi){
        window.location.href = _doi;
    }


    render(){
        var back = ['black', '', ''];
        var font = ['white', '', ''];
        var compu = ''
        if(this.state.pub_kind === 'intelligence'){
            back = ['black', '', ''];
            font = ['white', '', ''];
            compu = ComIntelligence.map((item, index) => {
                return(
                    <div id='pub-per'>
                        <div id='per-circle'><img src={circle}/>&emsp;</div>
                        <div id='per-text'>
                            <b>{item.title}</b> <br/>
                            {item.authors} <br/>
                            <small>{item.year} - {item['journal/conference']}</small> <br/>
                        </div>
                        <div id='per-doi'>
                            <button doi={item.DOI} onClick={function(e){ window.open(e.target.getAttribute('doi')); }.bind(this)}>
                                <img src={linkimg}/>
                                DOI
                            </button>
                        </div>
                        <div id='per-empty'></div>
                    </div>
                )
            });
        }
        else if(this.state.pub_kind === 'finance'){
            back = ['', 'black', ''];
            font = ['', 'white', ''];
            compu = ComFinance.map((item, index) => {
                return(
                    <div id='pub-per'>
                        <div id='per-circle'><img src={circle}/>&emsp;</div>
                        <div id='per-text'>
                            <b>{item.title}</b> <br/>
                            {item.authors} <br/>
                            <small>{item.year} - {item['journal/conference']}</small> <br/>
                        </div>
                        <div id='per-doi'>
                            <button doi={item.DOI} onClick={function(e){ window.open(e.target.getAttribute('doi')); }.bind(this)}>
                                <img src={linkimg}/>
                                DOI
                            </button>
                        </div>
                        <div id='per-empty'></div>
                    </div>
                )
            });
        }
        else if(this.state.pub_kind === 'biology'){
            back = ['', '', 'black'];
            font = ['', '', 'white'];
            compu = ComBiology.map((item, index) => {
                return(
                    <div id='pub-per'>
                        <div id='per-circle'><img src={circle}/>&emsp;</div>
                        <div id='per-text'>
                            <b>{item.title}</b> <br/>
                            {item.authors} <br/>
                            <small>{item.year} - {item['journal/conference']}</small> <br/>
                        </div>
                        <div id='per-doi'>
                            <button doi={item.DOI} onClick={function(e){ window.open(e.target.getAttribute('doi')); }.bind(this)}>
                                <img src={linkimg}/>
                                DOI
                            </button>
                        </div>
                        <div id='per-empty'></div>
                    </div>
                )
            });
        }
        

        return(
            <div className='pub'>
                <div id='pub-top'>
                    <p id='pub-title'>Publication</p>
                </div>

                <div id='pub-kind-btn'>
                    <button style={{background:back[0], color:font[0]}} onClick={() => this.changePubKind('intelligence')}>Intelligence</button>
                    <button style={{background:back[1], color:font[1]}} onClick={() => this.changePubKind('finance')}>Finance</button>
                    <button style={{background:back[2], color:font[2]}} onClick={() => this.changePubKind('biology')}>Biology</button>
                </div>

                <div id='pub-section'>
                    {compu}
                </div>
            </div>
        );
    }
}

export default Publication;